@import '../../assets/scss/com.scss';
.platform{
    width: 100%;
    height: 100%;
    background-color: $pageBackColor;
    .platform_main{
        width: 100%;
        height: 100%;
        .main_box{
            width: 100%;
            height: 156px;
            background: #FFFFFF;
            margin-top: 20px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 0px 30px;
            box-sizing: border-box;
            .box_left{
                font-size: 30px;
                font-family: Microsoft YaHei;
                font-weight: 400;
                color: #7E7E7E;
                .left_top{
                    .top_span{
                        display: inline-block;
                        width: 91px;
                    }
                    .spans{
                        display: inline-block;
                        text-indent: 28px;
                        font-size: 30px;
                        font-family: Microsoft YaHei;
                        font-weight: 400;
                        color: #363636;
                    }
                }
                .left_fl{
                    margin-top: 28px;
                    .top_span{
                        display: inline-block;
                        width: 91px;
                    }
                    .spans{
                        display: inline-block;
                        text-indent: 28px;
                        font-size: 30px;
                        font-family: Microsoft YaHei;
                        font-weight: 400;
                        color: #363636;
                    }
                }
            }
            .box_right{
                display: flex;
                align-items: center;
                .right_line{
                    width: 2px;
                    height: 112px;
                    background: #B7B7B7;
                    margin: 0 30px;
                }
            }
        }
    }
    .platform_top{
        width: 100%;
        height: 114px;
        background: $myBackColor;
        display: flex;
        align-items: center;
        font-size: 36px;
        font-family: Microsoft YaHei;
        font-weight: 400;
        color: #FFFFFF;
        padding-left: 120px;
        box-sizing: border-box;
    }
}